import { useState, useEffect } from 'react';
import { Search, Filter, X, ChevronLeft, ChevronRight, Eye, Package, User, MapPin, Phone, Mail, Calendar, CreditCard, ExternalLink } from 'lucide-react';
import { useDispatch, useSelector } from 'react-redux';
import { updateOrderStatusRequest } from '../../store/slices/orderSlice';

const OrderManagement = () => {
  const dispatch = useDispatch();
  const { orders } = useSelector((state) => state.orders);
  const { token } = useSelector((state) => state.auth);
  const API_BASE_URL = "https://thirushipickles-backend-1.onrender.com";
  const [filteredOrders, setFilteredOrders] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [selectedOrderDetails, setSelectedOrderDetails] = useState(null);
  const [showDetailsDialog, setShowDetailsDialog] = useState(false);
  const [loadingDetails, setLoadingDetails] = useState(false);
  const [filters, setFilters] = useState({
    orderId: '',
    customerName: '',
    status: '',
    fromDate: '',
    toDate: ''
  });

  useEffect(() => {
    setFilteredOrders(orders);
  }, [orders]);

  const handleFilter = () => {
    let filtered = [...orders];

    if (filters.orderId) {
      filtered = filtered.filter(order => 
        order.id.toString().includes(filters.orderId)
      );
    }

    if (filters.customerName) {
      const searchLower = filters.customerName.toLowerCase();
      filtered = filtered.filter(order => 
        order.customer?.fullName?.toLowerCase().includes(searchLower)
      );
    }

    if (filters.status) {
      filtered = filtered.filter(order => order.status === filters.status);
    }

    if (filters.fromDate) {
      filtered = filtered.filter(order => 
        new Date(order.createdAt) >= new Date(filters.fromDate)
      );
    }

    if (filters.toDate) {
      filtered = filtered.filter(order => 
        new Date(order.createdAt) <= new Date(filters.toDate)
      );
    }

    setFilteredOrders(filtered);
    setPage(0);
  };

  const clearFilters = () => {
    setFilters({
      orderId: '',
      customerName: '',
      status: '',
      fromDate: '',
      toDate: ''
    });
    setFilteredOrders(orders);
  };

  const handleOrderStatusChange = (orderId, status) => {
    dispatch(updateOrderStatusRequest({ id: orderId, status }));
  };

  const fetchOrderDetails = async (orderId) => {
    setLoadingDetails(true);
    try {
      const response = await fetch(
        `${API_BASE_URL}/api/admin/orders/${orderId}/details`,
        {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        }
      );
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        console.error('Error response:', errorData);
        throw new Error(errorData.message || 'Failed to fetch order details');
      }
      
      const data = await response.json();
      console.log('Fetched order details:', data);
      
      if (data && data.length > 0) {
        const transformedData = {
          orderId: data[0].orderId,
          orderDate: data[0].createdAt,
          orderStatus: data[0].status,
          totalAmount: data[0].totalAmount,
          paymentMethod: 'COD',
          customerName: data[0].username,
          customerEmail: data[0].email || 'N/A',
          customerPhone: data[0].phoneNumber,
          mapLink: data[0].mapLink || null, // Add mapLink
          shippingAddress: {
            addressLine1: data[0].deliveryAddress || 'N/A',
            addressLine2: data[0].notes || '',
            city: data[0].deliveryCity || '',
            pincode: data[0].deliveryPincode || '',
            country: 'India'
          },
          items: data.map(item => ({
            pickleName: item.itemName,
            quantity: item.quantity,
            price: item.itemPrice,
            totalPrice: item.subtotal
          }))
        };
        
        setSelectedOrderDetails(transformedData);
        setShowDetailsDialog(true);
      } else {
        alert('Order details not found');
      }
    } catch (error) {
      console.error('Error fetching order details:', error);
      alert(`Failed to fetch order details: ${error.message}`);
    } finally {
      setLoadingDetails(false);
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      'PENDING': 'bg-white text-blue-700 border-grey-100',
      'CONFIRMED': 'bg-white text-green-600 border-grey-100',
      'PROCESSING': 'bg-white text-yellow-500 border-grey-100',
      'SHIPPED': 'bg-white text-orange-600 border-grey-100',
      'DELIVERED': 'bg-white text-white border-grey-100',
      'CANCELLED': 'bg-white text-red-500 border-grey-100'
    };
    return colors[status] || 'bg-gray-100 text-gray-700';
  };

  const totalPages = Math.ceil(filteredOrders.length / rowsPerPage);
  const paginatedOrders = filteredOrders.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  return (
    <div className="min-h-screen bg-white p-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-blue-800 mb-2">Orders</h1>
          <p className="text-black text-sm">Manage and track all orders</p>
        </div>

        <div className="bg-white border-2 border-gray-200 rounded-xl shadow-lg p-6 mb-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-6 gap-4 mb-6">
            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">Order ID</label>
              <input
                type="text"
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="Search ID"
                value={filters.orderId}
                onChange={(e) => setFilters({...filters, orderId: e.target.value})}
              />
            </div>

            <div className="lg:col-span-2">
              <label className="block text-sm font-semibold text-blue-700 mb-2">Customer Name</label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-blue-800 w-5 h-5" />
                <input
                  type="text"
                  className="w-full pl-10 pr-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-800"
                  placeholder="Search customer"
                  value={filters.customerName}
                  onChange={(e) => setFilters({...filters, customerName: e.target.value})}
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">Status</label>
              <select
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white"
                value={filters.status}
                onChange={(e) => setFilters({...filters, status: e.target.value})}
              >
                <option value="">All</option>
                <option value="PENDING">Pending</option>
                <option value="CONFIRMED">Confirmed</option>
                <option value="PROCESSING">Processing</option>
                <option value="SHIPPED">Shipped</option>
                <option value="DELIVERED">Delivered</option>
                <option value="CANCELLED">Cancelled</option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">From Date</label>
              <input
                type="date"
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={filters.fromDate}
                onChange={(e) => setFilters({...filters, fromDate: e.target.value})}
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">To Date</label>
              <input
                type="date"
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={filters.toDate}
                onChange={(e) => setFilters({...filters, toDate: e.target.value})}
              />
            </div>
          </div>

          <div className="flex gap-3">
            <button
              onClick={handleFilter}
              className="flex items-center gap-2 px-6 py-2.5 bg-green-500 text-white font-semibold rounded-lg hover:bg-green-700 transition-all shadow-md"
            >
              <Filter className="w-5 h-5" />
              Filter
            </button>
            <button
              onClick={clearFilters}
              className="flex items-center gap-2 px-6 py-2.5 bg-red-500 text-white font-semibold border-2 border-red-500 rounded-lg hover:bg-red-700 transition-all"
            >
              <X className="w-5 h-5" />
              Clear
            </button>
          </div>
        </div>

        <div className="bg-white border-2 border-gray-200 rounded-xl shadow-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="bg-blue-900 text-white">
                  <th className="px-6 py-4 text-left text-sm font-bold">Order ID</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Customer</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Total Amount</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Status</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Date</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-blue-100">
                {paginatedOrders.map((order) => (
                  <tr key={order.id} className="hover:bg-blue-100 transition-colors">
                    <td className="px-6 py-4 font-bold text-orange-500">#{order.id}</td>
                    <td className="px-6 py-4 font-serif font-bold text-gray-900">{order.customer?.fullName}</td>
                    <td className="px-6 py-4 font-bold text-green-600">₹{order.totalAmount}</td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex px-3 py-1 text-xs font-bold rounded-full border-2 ${getStatusColor(order.status)}`}>
                        {order.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-gray-700">{new Date(order.createdAt).toLocaleDateString()}</td>
                    <td className="px-6 py-4">
                      <div className="flex gap-2">
                        <button
                          onClick={() => fetchOrderDetails(order.id)}
                          disabled={loadingDetails}
                          className="px-3 py-2 bg-orange-600 text-white rounded-lg hover:bg-blue-700 transition-all flex items-center gap-2 font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                        >
                          <Eye className="w-4 h-4" />
                          View
                        </button>
                        <select
                          className="px-3 py-2 border-2 border-blue-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white font-medium text-blue-700 cursor-pointer hover:bg-blue-50"
                          value={order.status}
                          onChange={(e) => handleOrderStatusChange(order.id, e.target.value)}
                        >
                          <option value="PENDING">Pending</option>
                          <option value="CONFIRMED">Confirmed</option>
                          <option value="PROCESSING">Processing</option>
                          <option value="SHIPPED">Shipped</option>
                          <option value="DELIVERED">Delivered</option>
                          <option value="CANCELLED">Cancelled</option>
                        </select>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <div className="bg-blue-50 px-6 py-4 border-t-2 border-blue-100">
            <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
              <div className="flex items-center gap-3">
                <label className="text-sm font-semibold text-blue-700">Rows per page:</label>
                <select
                  className="px-3 py-2 border-2 border-blue-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white font-medium text-blue-700"
                  value={rowsPerPage}
                  onChange={(e) => {
                    setRowsPerPage(parseInt(e.target.value, 10));
                    setPage(0);
                  }}
                >
                  <option value={5}>5</option>
                  <option value={10}>10</option>
                  <option value={25}>25</option>
                  <option value={50}>50</option>
                </select>
              </div>

              <div className="flex items-center gap-4">
                <span className="text-sm font-medium text-blue-700">
                  Page {page + 1} of {totalPages || 1}
                </span>
                <div className="flex gap-2">
                  <button
                    onClick={() => setPage(Math.max(0, page - 1))}
                    disabled={page === 0}
                    className="p-2 rounded-lg border-2 border-blue-300 text-blue-600 hover:bg-blue-600 hover:text-white disabled:opacity-40 disabled:cursor-not-allowed transition-all"
                  >
                    <ChevronLeft className="w-5 h-5" />
                  </button>
                  <button
                    onClick={() => setPage(Math.min(totalPages - 1, page + 1))}
                    disabled={page >= totalPages - 1 || filteredOrders.length === 0}
                    className="p-2 rounded-lg border-2 border-blue-300 text-blue-600 hover:bg-blue-600 hover:text-white disabled:opacity-40 disabled:cursor-not-allowed transition-all"
                  >
                    <ChevronRight className="w-5 h-5" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Order Details Dialog */}
      {showDetailsDialog && selectedOrderDetails && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl shadow-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
            <div className="sticky top-0 bg-blue-600 text-white px-6 py-4 flex justify-between items-center rounded-t-2xl">
              <div>
                <h2 className="text-2xl font-bold">Order Details</h2>
                <p className="text-blue-100 text-sm">Order #{selectedOrderDetails.orderId}</p>
              </div>
              <button
                onClick={() => setShowDetailsDialog(false)}
                className="p-2 hover:bg-blue-700 rounded-lg transition-all"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="p-6 space-y-6">
              {/* Order Summary */}
              <div className="bg-blue-50 border-2 border-blue-200 rounded-xl p-5">
                <div className="flex items-center gap-2 mb-4">
                  <Package className="w-5 h-5 text-blue-600" />
                  <h3 className="text-lg font-bold text-blue-700">Order Summary</h3>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-gray-600">Order Date</p>
                    <p className="font-semibold text-gray-900 flex items-center gap-2">
                      <Calendar className="w-4 h-4 text-blue-600" />
                      {new Date(selectedOrderDetails.orderDate).toLocaleDateString()}
                    </p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Status</p>
                    <span className={`inline-flex px-3 py-1 text-xs font-bold rounded-full border-2 ${getStatusColor(selectedOrderDetails.orderStatus)}`}>
                      {selectedOrderDetails.orderStatus}
                    </span>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Total Amount</p>
                    <p className="font-bold text-2xl text-blue-600">₹{selectedOrderDetails.totalAmount}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Payment Method</p>
                    <p className="font-semibold text-gray-900 flex items-center gap-2">
                      <CreditCard className="w-4 h-4 text-blue-600" />
                      {selectedOrderDetails.paymentMethod}
                    </p>
                  </div>
                </div>
              </div>

              {/* Customer Information */}
              <div className="bg-indigo-50 border-2 border-indigo-200 rounded-xl p-5">
                <div className="flex items-center gap-2 mb-4">
                  <User className="w-5 h-5 text-indigo-600" />
                  <h3 className="text-lg font-bold text-indigo-700">Customer Information</h3>
                </div>
                <div className="space-y-3">
                  <div>
                    <p className="text-sm text-gray-600">Name</p>
                    <p className="font-semibold text-gray-900">{selectedOrderDetails.customerName}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 flex items-center gap-1">
                      <Mail className="w-4 h-4" />
                      Email
                    </p>
                    <p className="font-medium text-gray-900">{selectedOrderDetails.customerEmail}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 flex items-center gap-1">
                      <Phone className="w-4 h-4" />
                      Phone
                    </p>
                    <p className="font-medium text-gray-900">{selectedOrderDetails.customerPhone}</p>
                  </div>
                </div>
              </div>

              {/* Delivery Address */}
              {selectedOrderDetails.shippingAddress && (
                <div className="bg-cyan-50 border-2 border-cyan-200 rounded-xl p-5">
                  <div className="flex items-center gap-2 mb-4">
                    <MapPin className="w-5 h-5 text-cyan-600" />
                    <h3 className="text-lg font-bold text-cyan-700">Delivery Address</h3>
                  </div>
                  <div className="space-y-2">
                    <p className="font-medium text-gray-900">{selectedOrderDetails.shippingAddress.addressLine1}</p>
                    {selectedOrderDetails.shippingAddress.addressLine2 && (
                      <div className="bg-blue-50 border border-blue-200 rounded-lg p-3 mt-2">
                        <p className="text-sm text-gray-600 font-semibold mb-1">Special Notes:</p>
                        <p className="text-gray-700 italic">{selectedOrderDetails.shippingAddress.addressLine2}</p>
                      </div>
                    )}
                    
                    {/* Display Map Link if available */}
                    {selectedOrderDetails.mapLink && (
                      <div className="bg-green-50 border border-green-200 rounded-lg p-3 mt-3">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="text-sm text-gray-600 font-semibold mb-1">📍 Customer Location:</p>
                            <p className="text-gray-700 text-sm">Click to view on Google Maps</p>
                          </div>
                          <a
                            href={selectedOrderDetails.mapLink}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="flex items-center gap-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-all font-medium"
                          >
                            <ExternalLink className="w-4 h-4" />
                            View Map
                          </a>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}

              {/* Order Items */}
              {selectedOrderDetails.items && selectedOrderDetails.items.length > 0 && (
                <div className="bg-slate-50 border-2 border-slate-200 rounded-xl p-5">
                  <h3 className="text-lg font-bold text-slate-700 mb-4">Order Items</h3>
                  <div className="space-y-3">
                    {selectedOrderDetails.items.map((item, index) => (
                      <div key={index} className="bg-white border border-slate-200 rounded-lg p-4 flex justify-between items-center">
                        <div className="flex-1">
                          <p className="font-semibold text-gray-900">{item.pickleName}</p>
                          <p className="text-sm text-gray-600">Quantity: {item.quantity}</p>
                          <p className="text-sm text-gray-600">Price per unit: ₹{item.price}</p>
                        </div>
                        <div className="text-right">
                          <p className="font-bold text-lg text-blue-600">₹{item.totalPrice}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                  <div className="mt-4 pt-4 border-t-2 border-slate-300 flex justify-between items-center">
                    <p className="text-lg font-bold text-gray-900">Total Amount:</p>
                    <p className="text-2xl font-bold text-blue-600">₹{selectedOrderDetails.totalAmount}</p>
                  </div>
                </div>
              )}
            </div>

            <div className="sticky bottom-0 bg-gray-100 px-6 py-4 rounded-b-2xl border-t-2 border-gray-200">
              <button
                onClick={() => setShowDetailsDialog(false)}
                className="w-full px-6 py-3 bg-blue-600 text-white font-bold rounded-lg hover:bg-blue-700 transition-all"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default OrderManagement;