import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { LogOut, User, Menu, X } from 'lucide-react';
import { logout } from '../store/slices/authSlice';
import { fetchPicklesRequest } from '../store/slices/pickleSlice';
import { fetchOrdersRequest } from '../store/slices/orderSlice';
import PickleManagement from '../components/AdminComponents/PickleManagement';
import OrderManagement from '../components/AdminComponents/OrderManagement';
import ContactManagement from '../components/AdminComponents/ContactManagement';
import BrandLogo from '../assets/BrandLogo.png';

const AdminDashboard = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);
  
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [tabValue, setTabValue] = useState(0);
  
  useEffect(() => {
    dispatch(fetchPicklesRequest());
    dispatch(fetchOrdersRequest());
  }, [dispatch]);

  const tabs = [
    { label: 'Pickle', component: PickleManagement },
    { label: 'Order', component: OrderManagement },
    { label: 'Contact', component: ContactManagement }
  ];

  const handleLogout = () => {
    dispatch(logout());
    navigate('/login');
  };

  const handleTabChange = (index) => {
    setTabValue(index);
    setDrawerOpen(false);
  };

  const ActiveComponent = tabs[tabValue].component;

  return (
    <div className="min-h-screen bg-gray-50">
      {/* AppBar */}
      <nav className="sticky top-0 bg-blue-900 text-white shadow-lg z-50">
        <div className="px-4">
          <div className="flex items-center justify-between h-16">
            {/* Mobile Menu Button */}
            <button
              onClick={() => setDrawerOpen(!drawerOpen)}
              className="md:hidden p-2 rounded-md hover:bg-blue-800 transition-colors"
            >
              <Menu size={24} />
            </button>

            {/* Logo */}
            <div className="bg-white px-4 py-2 rounded flex items-center gap-3">
              <img src={BrandLogo} alt="Brand Logo" className="h-10 w-10 object-contain" />
              <h1 className="text-lg font-black text-blue-600 tracking-wide hidden md:block">
                Thirushi Pickles
              </h1>
            </div>

            {/* Desktop Tabs */}
            <div className="hidden md:flex gap-2 ml-80">
              {tabs.map((tab, index) => (
                <button
                  key={tab.label}
                  onClick={() => handleTabChange(index)}
                  className={`px-6 py-4 font-medium transition-all ${
                    tabValue === index
                      ? 'border-b-4 border-white font-bold'
                      : 'border-b-4 border-transparent hover:bg-blue-800'
                  }`}
                >
                  {tab.label}
                </button>
              ))}
            </div>

            <div className="flex-grow"></div>

            {/* User Info & Logout */}
            <div className="flex items-center gap-4">
              <div className="flex items-center gap-2">
                <div className="w-8 h-8 bg-white rounded-full flex items-center justify-center">
                  <User size={18} className="text-blue-800" />
                </div>
                <span className="hidden sm:block font-semibold text-sm">
                  {user?.username}
                </span>
              </div>
              
              <button
                onClick={handleLogout}
                className="flex items-center gap-2 px-4 py-2 border border-white rounded hover:bg-blue-800 transition-colors"
              >
                <LogOut size={18} />
                <span className="hidden sm:inline">Logout</span>
              </button>
            </div>
          </div>
        </div>
      </nav>

      {/* Mobile Drawer */}
      {drawerOpen && (
        <>
          {/* Backdrop */}
          <div
            className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
            onClick={() => setDrawerOpen(false)}
          ></div>

          {/* Drawer */}
          <div className="fixed left-0 top-0 h-full w-64 bg-white shadow-xl z-50 md:hidden">
            <div className="p-4">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-bold text-blue-900">Menu</h2>
                <button
                  onClick={() => setDrawerOpen(false)}
                  className="p-2 rounded-md hover:bg-gray-100"
                >
                  <X size={24} />
                </button>
              </div>
              
              <ul className="space-y-2">
                {tabs.map((tab, index) => (
                  <li key={tab.label}>
                    <button
                      onClick={() => handleTabChange(index)}
                      className={`w-full text-left px-4 py-3 rounded-lg transition-colors ${
                        tabValue === index
                          ? 'bg-blue-900 text-white font-semibold'
                          : 'hover:bg-gray-100 text-gray-700'
                      }`}
                    >
                      {tab.label}
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </>
      )}

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 py-6">
        <ActiveComponent />
      </div>
    </div>
  );
};

export default AdminDashboard;