import React, { useState } from 'react';
import {
  Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField,
  MenuItem, Chip, Alert
} from '@mui/material';
import { Add, Edit, Delete, Search } from '@mui/icons-material';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { useDispatch, useSelector } from 'react-redux';
import {
  createPickleRequest,
  updatePickleRequest,
  deletePickleRequest,
} from '../../store/slices/pickleSlice';

const PickleManagement = () => {
  const dispatch = useDispatch();
  const { pickles } = useSelector((state) => state.pickles);
  
  const [openDialog, setOpenDialog] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [currentPickle, setCurrentPickle] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('ALL');
  const [showCategoryMenu, setShowCategoryMenu] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    price: '',
    stockQuantity: '',
    baseWeight: '250',
    imageUrl: '',
    category: 'VEG',
  });

  const handleOpenDialog = (pickle = null) => {
    if (pickle) {
      setEditMode(true);
      setCurrentPickle(pickle);
      setFormData({
        ...pickle,
        baseWeight: pickle.baseWeight || '250'
      });
    } else {
      setEditMode(false);
      setCurrentPickle(null);
      setFormData({
        name: '',
        description: '',
        price: '',
        stockQuantity: '',
        baseWeight: '250',
        imageUrl: '',
        category: 'VEG',
      });
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditMode(false);
    setCurrentPickle(null);
  };

  const handleSubmit = () => {
    if (editMode) {
      dispatch(updatePickleRequest({ id: currentPickle.id, data: formData }));
      setSuccessMessage('Pickle updated successfully!');
    } else {
      dispatch(createPickleRequest(formData));
      setSuccessMessage('New pickle added successfully!');
    }
    handleCloseDialog();
    setTimeout(() => setSuccessMessage(''), 4000);
  };

  const handleDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this pickle?')) {
      dispatch(deletePickleRequest(id));
      setSuccessMessage('Pickle deleted successfully!');
      setTimeout(() => setSuccessMessage(''), 4000);
    }
  };

  const filteredPickles = pickles.filter((pickle) => {
    const matchesSearch = pickle.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         pickle.description?.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'ALL' || pickle.category === selectedCategory;
    return matchesSearch && matchesCategory;
  });

  const categories = [
    { label: 'All', value: 'ALL' },
    { label: 'Vegetarian', value: 'VEG' },
    { label: 'Non-Vegetarian', value: 'NON_VEG' },
    { label: 'Mixed', value: 'MIXED' }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
      {successMessage && (
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-4">
          <Alert 
            severity="success" 
            onClose={() => setSuccessMessage('')}
            className="animate-in fade-in duration-300"
          >
            {successMessage}
          </Alert>
        </div>
      )}

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4 mb-8">
          <div>
            <h1 className="text-4xl font-bold text-blue-800 tracking-tight">
              Pickles
            </h1>
            <p className="text-red-600 mt-1">
              {filteredPickles.length} {filteredPickles.length === 1 ? 'pickle' : 'pickles'} available
            </p>
          </div>
          <button
            onClick={() => handleOpenDialog()}
            className="inline-flex items-center justify-center px-6 py-3 rounded-lg font-semibold text-white bg-blue-900 hover:bg-blue-700 transition-colors shadow-md hover:shadow-lg"
          >
            <Add fontSize="small" className="mr-2" />
            Add Pickle
          </button>
        </div>

        <div className="bg-white rounded-lg border border-gray-200 p-4 mb-8 shadow-sm">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="md:col-span-2">
              <div className="relative">
                <Search className="absolute left-3 top-3 text-gray-400" fontSize="small" />
                <input
                  type="text"
                  placeholder="Search by name or description..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-900 focus:border-transparent transition-colors"
                />
              </div>
            </div>

            <div className="relative">
              <button
                onClick={() => setShowCategoryMenu(!showCategoryMenu)}
                className="w-full inline-flex items-center justify-between px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors text-gray-700 font-medium"
              >
                <span>
                  {categories.find(c => c.value === selectedCategory)?.label}
                </span>
                <ExpandLessIcon
                  color="inherit"
                  fontSize="small"
                  className={`transition-transform ${showCategoryMenu ? 'rotate-180' : ''}`}
                />
              </button>

              {showCategoryMenu && (
                <div className="absolute z-50 w-full mt-2 bg-white border border-gray-200 rounded-lg shadow-lg">
                  {categories.map((category) => (
                    <button
                      key={category.value}
                      onClick={() => {
                        setSelectedCategory(category.value);
                        setShowCategoryMenu(false);
                      }}
                      className={`w-full text-left px-4 py-2 hover:bg-gray-100 transition-colors first:rounded-t-lg last:rounded-b-lg ${
                        selectedCategory === category.value ? 'bg-red-50 text-red-600 font-semibold' : 'text-gray-700'
                      }`}
                    >
                      {category.label}
                    </button>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        {filteredPickles.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {filteredPickles.map((pickle) => (
              <div 
                key={pickle.id}
                className="bg-white rounded-lg border border-gray-200 overflow-hidden hover:shadow-xl hover:border-blue-600 transition-all duration-300 flex flex-col h-full"
              >
                <div className="relative w-full h-48 bg-gray-100 overflow-hidden group">
                  <LazyLoadImage
                    src={pickle.imageUrl || 'https://via.placeholder.com/300x200?text=Pickle'}
                    alt={pickle.name}
                    className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                  />
                  <div className="absolute inset-0 bg-black opacity-0 group-hover:opacity-10 transition-opacity" />
                </div>

                <div className="p-4 flex-1 flex flex-col">
                  <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">
                    {pickle.name}
                  </h3>
                  
                  <p className="text-gray-600 text-sm mb-3 line-clamp-2 flex-1">
                    {pickle.description}
                  </p>

                  <div className="flex items-center justify-between mb-3">
                    <div>
                      <span className="text-2xl font-bold text-blue-900">
                        ₹{pickle.price}
                      </span>
                      <span className="text-sm text-gray-500 ml-1">/ {pickle.baseWeight || 250}g</span>
                    </div>
                    <Chip
                      label={
                        pickle.category === 'VEG' ? 'Veg' :
                        pickle.category === 'NON_VEG' ? 'Non-Veg' :
                        'Mixed'
                      }
                      size="small"
                      color={pickle.available ? 'success' : 'error'}
                      variant="outlined"
                      className="font-semibold"
                    />
                  </div>

                  <div className="mb-4 pb-4 border-b border-gray-200">
                    <p className="text-sm text-gray-600">
                      <span className="font-semibold">Stock:</span> {pickle.stockQuantity} {parseInt(pickle.stockQuantity) > 0 ? 'available' : 'out of stock'}
                    </p>
                  </div>
                </div>

                <div className="flex gap-2 px-4 pb-4">
                  <button
                    onClick={() => handleOpenDialog(pickle)}
                    className="flex-1 inline-flex items-center justify-center px-3 py-2 bg-blue-50 text-blue-600 rounded-lg hover:bg-blue-100 transition-colors font-medium text-sm"
                  >
                    <Edit fontSize="small" className="mr-1" />
                    Edit
                  </button>
                  <button
                    onClick={() => handleDelete(pickle.id)}
                    className="flex-1 inline-flex items-center justify-center px-3 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 transition-colors font-medium text-sm"
                  >
                    <Delete fontSize="small" className="mr-1" />
                    Delete
                  </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12 bg-white rounded-lg border border-gray-200">
            <p className="text-gray-600 text-lg mb-4">No pickles found</p>
            <button
              onClick={() => handleOpenDialog()}
              className="inline-flex items-center px-4 py-2 text-red-600 hover:text-red-700 font-medium"
            >
              <Add fontSize="small" className="mr-2" />
              Create your first pickle
            </button>
          </div>
        )}
      </div>

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle className="text-xl font-bold bg-gray-50 border-b border-gray-200">
          {editMode ? 'Edit Pickle' : 'Add New Pickle'}
        </DialogTitle>
        <DialogContent className="pt-6 space-y-4">
          <TextField
            fullWidth
            label="Name"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            placeholder="Enter pickle name"
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            label="Description"
            value={formData.description}
            onChange={(e) => setFormData({ ...formData, description: e.target.value })}
            placeholder="Enter description"
            multiline
            rows={3}
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            label="Base Weight (grams)"
            type="number"
            value={formData.baseWeight}
            onChange={(e) => setFormData({ ...formData, baseWeight: e.target.value })}
            placeholder="250"
            helperText="Base weight for price calculation (e.g., 250g)"
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            label={`Price for ${formData.baseWeight}g (₹)`}
            type="number"
            value={formData.price}
            onChange={(e) => setFormData({ ...formData, price: e.target.value })}
            placeholder="0.00"
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            label="Stock Quantity"
            type="number"
            value={formData.stockQuantity}
            onChange={(e) => setFormData({ ...formData, stockQuantity: e.target.value })}
            placeholder="0"
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            label="Image URL"
            value={formData.imageUrl}
            onChange={(e) => setFormData({ ...formData, imageUrl: e.target.value })}
            placeholder="https://example.com/image.jpg"
            variant="outlined"
            size="small"
          />
          <TextField
            fullWidth
            select
            label="Category"
            value={formData.category}
            onChange={(e) => setFormData({ ...formData, category: e.target.value })}
            variant="outlined"
            size="small"
          >
            <MenuItem value="VEG">Vegetarian</MenuItem>
            <MenuItem value="NON_VEG">Non-Vegetarian</MenuItem>
            <MenuItem value="MIXED">Mixed</MenuItem>
          </TextField>
        </DialogContent>
        <DialogActions className="p-4 border-t border-gray-200 gap-2">
          <Button 
            onClick={handleCloseDialog}
            className="text-gray-700 hover:bg-gray-100"
          >
            Cancel
          </Button>
          <Button 
            onClick={handleSubmit}
            variant="contained"
            className="bg-red-600 hover:bg-red-700 text-white"
          >
            {editMode ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default PickleManagement;