import React, { useState, useEffect } from 'react';
import { Search, Filter, X, ChevronLeft, ChevronRight, Eye } from 'lucide-react';

const ContactManagement = () => {
  const [contacts, setContacts] = useState([]);
  const [filteredContacts, setFilteredContacts] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [selectedContact, setSelectedContact] = useState(null);
  const [showDetailsDialog, setShowDetailsDialog] = useState(false);
  const [filters, setFilters] = useState({
    searchTerm: '',
    status: '',
    fromDate: '',
    toDate: ''
  });
  const API_BASE_URL = "https://thirushipickles-backend-1.onrender.com";
  useEffect(() => {
    fetchContacts();
  }, []);

  const fetchContacts = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`${API_BASE_URL}/api/contact/all`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      const data = await response.json();
      const contactsArray = Array.isArray(data) ? data : [];
      setContacts(contactsArray);
      setFilteredContacts(contactsArray);
    } catch (error) {
      console.error('Error fetching contacts:', error);
      setContacts([]);
      setFilteredContacts([]);
    }
  };

  const handleFilter = () => {
    if (!Array.isArray(contacts)) {
      setFilteredContacts([]);
      return;
    }

    let filtered = [...contacts];

    if (filters.searchTerm) {
      const searchLower = filters.searchTerm.toLowerCase();
      filtered = filtered.filter(contact => 
        contact.name?.toLowerCase().includes(searchLower) ||
        contact.email?.toLowerCase().includes(searchLower)
      );
    }

    if (filters.status) {
      filtered = filtered.filter(contact => contact.status === filters.status);
    }

    if (filters.fromDate) {
      filtered = filtered.filter(contact => 
        new Date(contact.createdAt) >= new Date(filters.fromDate)
      );
    }

    if (filters.toDate) {
      filtered = filtered.filter(contact => 
        new Date(contact.createdAt) <= new Date(filters.toDate)
      );
    }

    setFilteredContacts(filtered);
    setPage(0);
  };

  const clearFilters = () => {
    setFilters({
      searchTerm: '',
      status: '',
      fromDate: '',
      toDate: ''
    });
    setFilteredContacts(contacts);
  };

  const handleContactStatusChange = async (contactId, newStatus) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`${API_BASE_URL}/api/contact/${contactId}/status`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ status: newStatus })
      });

      if (response.ok) {
        fetchContacts();
      }
    } catch (error) {
      console.error('Error updating contact status:', error);
    }
  };

  const handleViewDetails = (contact) => {
    setSelectedContact(contact);
    setShowDetailsDialog(true);
  };

  const getStatusColor = (status) => {
    const colors = {
      'NEW': 'bg-white text-blue-700 border-grey-100',
      'IN_PROGRESS': 'bg-white text-yellow-500 border-grey-100',
      'RESOLVED': 'bg-white text-green-600 border-grey-100'
    };
    return colors[status] || 'bg-gray-100 text-gray-700';
  };

  const totalPages = Math.ceil(filteredContacts.length / rowsPerPage);
  const paginatedContacts = filteredContacts.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  return (
    <div className="min-h-screen bg-white p-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-blue-800 mb-2">Contact Requests</h1>
          <p className="text-black text-sm">Manage and respond to customer inquiries</p>
        </div>

        <div className="bg-white border-2 border-gray-200 rounded-xl shadow-lg p-6 mb-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4 mb-6">
            <div className="lg:col-span-2">
              <label className="block text-sm font-semibold text-blue-800 mb-2">Search</label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-blue-800 w-5 h-5" />
                <input
                  type="text"
                  className="w-full pl-10 pr-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-800"
                  placeholder="Search by name or email"
                  value={filters.searchTerm}
                  onChange={(e) => setFilters({...filters, searchTerm: e.target.value})}
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">Status</label>
              <select
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white"
                value={filters.status}
                onChange={(e) => setFilters({...filters, status: e.target.value})}
              >
                <option value="">All</option>
                <option value="NEW">New</option>
                <option value="IN_PROGRESS">In Progress</option>
                <option value="RESOLVED">Resolved</option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">From Date</label>
              <input
                type="date"
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={filters.fromDate}
                onChange={(e) => setFilters({...filters, fromDate: e.target.value})}
              />
            </div>

            <div>
              <label className="block text-sm font-semibold text-blue-800 mb-2">To Date</label>
              <input
                type="date"
                className="w-full px-4 py-2.5 border-2 border-grey-500 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                value={filters.toDate}
                onChange={(e) => setFilters({...filters, toDate: e.target.value})}
              />
            </div>
          </div>

          <div className="flex gap-3">
            <button
              onClick={handleFilter}
              className="flex items-center gap-2 px-6 py-2.5 bg-green-500 text-white font-semibold rounded-lg hover:bg-green-700 transition-all shadow-md"
            >
              <Filter className="w-5 h-5" />
              Filter
            </button>
            <button
              onClick={clearFilters}
              className="flex items-center gap-2 px-6 py-2.5 bg-red-500 text-white font-semibold border-2 border-red-500 rounded-lg hover:bg-red-700 transition-all"
            >
              <X className="w-5 h-5" />
              Clear
            </button>
          </div>
        </div>

        <div className="bg-white border-2 border-gray-200 rounded-xl shadow-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="bg-blue-900 text-white">
                  <th className="px-6 py-4 text-left text-sm font-bold">ID</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Name</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Email</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Subject</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Status</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Date</th>
                  <th className="px-6 py-4 text-left text-sm font-bold">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-blue-100">
                {paginatedContacts.map((contact) => (
                  <tr key={contact.id} className="hover:bg-blue-100 transition-colors">
                    <td className="px-6 py-4 font-bold text-orange-500">#{contact.id}</td>
                    <td className="px-6 py-4 font-serif font-bold text-gray-900">{contact.name}</td>
                    <td className="px-6 py-4 text-gray-700">{contact.email}</td>
                    <td className="px-6 py-4 text-gray-900">{contact.subject}</td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex px-3 py-1 text-xs font-bold rounded-full border-2 ${getStatusColor(contact.status || 'NEW')}`}>
                        {contact.status || 'NEW'}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-gray-700">{new Date(contact.createdAt).toLocaleDateString()}</td>
                    <td className="px-6 py-4">
                      <div className="flex gap-2">
                        <button
                          onClick={() => handleViewDetails(contact)}
                          className="px-3 py-2 bg-orange-600 text-white rounded-lg hover:bg-blue-700 transition-all flex items-center gap-2 font-medium"
                        >
                          <Eye className="w-4 h-4" />
                          View
                        </button>
                        <select
                          className="px-3 py-2 border-2 border-blue-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white font-medium text-blue-700 cursor-pointer hover:bg-blue-50"
                          value={contact.status || 'NEW'}
                          onChange={(e) => handleContactStatusChange(contact.id, e.target.value)}
                        >
                          <option value="NEW">New</option>
                          <option value="IN_PROGRESS">In Progress</option>
                          <option value="RESOLVED">Resolved</option>
                        </select>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <div className="bg-blue-50 px-6 py-4 border-t-2 border-blue-100">
            <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
              <div className="flex items-center gap-3">
                <label className="text-sm font-semibold text-blue-700">Rows per page:</label>
                <select
                  className="px-3 py-2 border-2 border-blue-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white font-medium text-blue-700"
                  value={rowsPerPage}
                  onChange={(e) => {
                    setRowsPerPage(parseInt(e.target.value, 10));
                    setPage(0);
                  }}
                >
                  <option value={5}>5</option>
                  <option value={10}>10</option>
                  <option value={25}>25</option>
                  <option value={50}>50</option>
                </select>
              </div>

              <div className="flex items-center gap-4">
                <span className="text-sm font-medium text-blue-700">
                  Page {page + 1} of {totalPages || 1}
                </span>
                <div className="flex gap-2">
                  <button
                    onClick={() => setPage(Math.max(0, page - 1))}
                    disabled={page === 0}
                    className="p-2 rounded-lg border-2 border-blue-300 text-blue-600 hover:bg-blue-600 hover:text-white disabled:opacity-40 disabled:cursor-not-allowed transition-all"
                  >
                    <ChevronLeft className="w-5 h-5" />
                  </button>
                  <button
                    onClick={() => setPage(Math.min(totalPages - 1, page + 1))}
                    disabled={page >= totalPages - 1 || filteredContacts.length === 0}
                    className="p-2 rounded-lg border-2 border-blue-300 text-blue-600 hover:bg-blue-600 hover:text-white disabled:opacity-40 disabled:cursor-not-allowed transition-all"
                  >
                    <ChevronRight className="w-5 h-5" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Contact Details Dialog */}
      {showDetailsDialog && selectedContact && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="sticky top-0 bg-blue-600 text-white px-6 py-4 flex justify-between items-center rounded-t-2xl">
              <div>
                <h2 className="text-2xl font-bold">Contact Details</h2>
                <p className="text-blue-100 text-sm">Contact #{selectedContact.id}</p>
              </div>
              <button
                onClick={() => setShowDetailsDialog(false)}
                className="p-2 hover:bg-blue-700 rounded-lg transition-all"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="p-6 space-y-6">
              <div className="bg-blue-50 border-2 border-blue-200 rounded-xl p-5">
                <h3 className="text-lg font-bold text-blue-700 mb-4">Contact Information</h3>
                <div className="space-y-3">
                  <div>
                    <p className="text-sm text-gray-600">Name</p>
                    <p className="font-semibold text-gray-900">{selectedContact.name}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Email</p>
                    <p className="font-medium text-gray-900">{selectedContact.email}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Subject</p>
                    <p className="font-medium text-gray-900">{selectedContact.subject}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Status</p>
                    <span className={`inline-flex px-3 py-1 text-xs font-bold rounded-full border-2 ${getStatusColor(selectedContact.status || 'NEW')}`}>
                      {selectedContact.status || 'NEW'}
                    </span>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Date</p>
                    <p className="font-medium text-gray-900">{new Date(selectedContact.createdAt).toLocaleDateString()}</p>
                  </div>
                </div>
              </div>

              <div className="bg-slate-50 border-2 border-slate-200 rounded-xl p-5">
                <h3 className="text-lg font-bold text-slate-700 mb-4">Message</h3>
                <div className="bg-white border border-slate-200 rounded-lg p-4">
                  <p className="text-gray-700 whitespace-pre-wrap">{selectedContact.message}</p>
                </div>
              </div>
            </div>

            <div className="sticky bottom-0 bg-gray-100 px-6 py-4 rounded-b-2xl border-t-2 border-gray-200">
              <button
                onClick={() => setShowDetailsDialog(false)}
                className="w-full px-6 py-3 bg-blue-600 text-white font-bold rounded-lg hover:bg-blue-700 transition-all"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ContactManagement;